@import './mixins.scss';

.skill-logos {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  img {
    height: 80px;
  }
}

.jobs {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

section.job {
  flex: 0 1 49%;
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid #aaa;
  border-right: 1px solid #aaa;
  font-size: 14px;
  margin: 1rem 0;
  background: #fff;

  @media screen and (min-width: 600px) {
    flex: 0 0 100%;
  }

  & > div {
    padding: 1rem;
  }

  .job-logo {
    width: 200px;
  }

  .job-details {
    display: flex;
    justify-content: space-between;
    flex-direction: row-reverse;
  }

  .job-description {
    flex: 0 0 60%;
    font-size: 14px;

    h3 {
      @include title;

      font-weight: bold;
      font-size: 18px;

      small {
        display: block;
        color: #aaa;
        font-size: 65%;
      }
    }
    em {
      font-style: normal;
      font-weight: 600;
      color: black;
      padding: 0 0.15em;
      background-color: #fffab0;
      border-bottom: 1px dotted black;
    }
  }

  .external-link {
    i {
      font-size: 14px;
      line-height: 18px;
      padding-left: 0.25em;
      margin-top: -0.2em;
    }
  }

  .job-tools {
    @include title;

    flex: 0 0 35%;

    ul {
      padding: 1rem 0;
      display: flex;
      flex-wrap: wrap;

      li {
        font-size: 16px;
        padding: 4px 4px 2px;
        margin: 4px;
      }
    }
  }

  .heading {
    color: #222;
    font-weight: 900;
    font-size: 1rem;
  }

  p.company {
    margin-top: 6px;
  }

  $base-colors: (
    'bluehouse': rgb(90, 33, 181),
    'epikone': #549dd2,
    'propeller': #2d2d2d,
    'ddc': #f60,
    'pwnie': #e35205,
    'frikin': #deb053,
  );

  @each $job, $base in $base-colors {
    &.#{$job} {
      a {
        color: $base;
        font-weight: 700;
      }
      & > div {
        border-left: $job-border-width solid $base;
      }
      .job-tools ul li {
        background-color: rgba($base, 0.2);
        box-shadow: 4px 4px 0 0 rgba(0, 0, 0, 0.3);
      }
    }
  }
}
