@import './mixins.scss';

header.main-header {
  @include title;

  color: #fff;
  background: linear-gradient(#000, #444);
  padding: 2rem 3rem 2rem 1rem;
  position: sticky;
  top: 0;
  height: 100vh;
  display: flex;
  flex-direction: column;

  h1 {
    font-size: 48px;
    line-height: 36px;
    margin-top: 0;

    small {
      display: block;
      font-size: 14px;
      line-height: 12px;
    }
  }

  p {
    margin-bottom: 12px;
  }

  &.section-header {
    padding-bottom: 10px;
    padding-top: 0px;
    background: #f5f5f5;
  }
}
